
header {
  background-color: #333336 !important;
  border-color: #333336 !important;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.35) !important;
  min-width: 100%;
  position: fixed;
}
.wrapper_header {
  display: flex;
  max-width: 970px;
  align-items: center;
  margin: 0 auto;
}
.logo {
  display: inline-block;
  padding-top: 6px;
}
.logo img {
  max-width: 36px;
  margin-right: 150px;
}
.wrapper_search {
  display: flex;
  align-items: center;
  margin-right: 30px;
}
.wrapper_search input {
  padding-left: 29px;
  padding-right: 60px;
  height: 31px;
  border: none;
  border-radius: 14px;
  background-color: rgba(119, 119, 119, 0.294);
  background-repeat: no-repeat;
  background-position: left;
  background-size: 20px;
  background-position-x: 5px;
  outline: none;
  color: #ffffff;
  background-image: url("https://xn--b1agoig6h.xn--p1ai/img/svg2/search_24.svg?3");
}
.wrapper_search input::placeholder {
}

.wrapper_search img {
  height: 15px;
  margin-right: -30px;
  background-color: rgba(119, 119, 119, 0.082);
}

.notifiactions {
  cursor: pointer;
  padding: 8px 12px;
}

.notifiactions:hover {
  background-color: rgba(119, 119, 119, 0.185);
}

.musik {
  cursor: pointer;
  max-width: 30px;
  padding: 8px;
  padding-left: 16px;
  padding-right: 40px;
}

.musik:hover {
  background-color: rgba(119, 119, 119, 0.185);
}

.header_about_me {
  margin-left: auto;
}
.header_aboutMe_flex {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  padding: 8px;
  padding-right: 0px;
}
.header_aboutMe_flex:hover {
  background-color: rgba(119, 119, 119, 0.185);
}
.header_aboutMe_flex h1 {
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
    "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
    "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
    "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
    sans-serif, arial, Tahoma, verdana;
  color: #fff !important;
  font-size: 12.5px;
  font-weight: 700;
  -webkit-font-smoothing: antialiased;
  white-space: nowrap;
}
.me {
  border-radius: 50%;
  margin-left: 15px;
  max-width: 23%;
}
.arrow {
  transform: rotate(90deg);
  margin-left: 10px;
}
.arrow img {
  max-width: 23%;
}

@media (max-width:992px){
    .logo img {
        margin-left: 10px;
      }
}

@media (max-width:730px){
    .wrapper_search input {
        padding-left: 19px;
        padding-right: 30px;
        height: 31px;
        background-size: 15px;
        background-position-x: 3px;
    }
    .logo img {
        max-width: 36px;
        margin-right: 30px;
      }

