.wrapper_sidebar{
    height: auto;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    max-width: 200px;
    flex-direction: column;
    background-color: transparent
}
.sidebar_nav{
    margin-top: 65px;
    padding: 5px;
    list-style: none;
}
.sidebar_nav li{
    font-family: -apple-system,BlinkMacSystemFont,'Roboto','Helvetica Neue',Geneva,"Noto Sans Armenian","Noto Sans Bengali","Noto Sans Cherokee","Noto Sans Devanagari","Noto Sans Ethiopic","Noto Sans Georgian","Noto Sans Hebrew","Noto Sans Kannada","Noto Sans Khmer","Noto Sans Lao","Noto Sans Osmanya","Noto Sans Tamil","Noto Sans Telugu","Noto Sans Thai",sans-serif,arial,Tahoma,verdana;
    color: #777!important;
    cursor: pointer;
    height: 28px;
    line-height: 27px;
    font-size: 12.5px;
    overflow: hidden;
    text-overflow: ellipsis;
}


.palka_nav{
    margin-top: 9px;
    padding-top: 9px;
    border-top: 1px solid #333336;
}
.li_domik{
    margin-left: 10px;
    margin-top: 4px;
}
.flex_my_profile{
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-right: 50px;
    padding-left: 15px;
    margin-left: -10px;
    height: 28px;
    
}
.flex_my_profile:hover{
    background-color: rgba(119, 119, 119, 0.239);
}
.flex_my_profile img{
    max-width: 18%;
}




